
body {
  background-color: silver;
}
.modal {
  padding: 15px;
}
.contact {
  position: relative;
  height: 230px;
}
.contact.adding {
    display: table;
    width: 100%;
    text-align: center;
}
.contact.adding .inner {
      display: table-cell;
      vertical-align: middle;
}
.contact i {
    cursor: pointer;
}
.contact h5, .contact h4 {
    font-weight: bold;
    margin: 10px 0;
}
.contact h4 {
    font-size: 20px;
}
.contact h5 {
    font-size: 16px;
    margin-bottom: 0;
}
.contact p {
    margin: 6px 0;
    line-height: 1;
}
.contact p.location {
      margin: 10px 0 15px;
}
.contact p.location i, .contact p.location span {
        display: inline-block;
        vertical-align: middle;
}
.contact p.location i {
        margin-top: -2px;
        font-size: 15px;
        margin-right: 5px;
}
.skill {
  display: inline-block;
  line-height: 1;
  font-weight: bold;
  margin-top: 8px;
  font-size: 16px;
}
.buttons-block {
  position: absolute;
  bottom: 10px;
  right: 15px;
}
.buttons-block i {
    display: inline-block;
    font-size: 20px;
    margin-left: 3px;
    vertical-align: middle;
}
